.dropdown {
  position: relative;
  display: inline-block;
  height: 100%;
  z-index: 2000;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 0 16px rgba(0,0,0,1);
  padding: 12px 16px;
  z-index: 1;
  left: calc((100% - 200px)/2);
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropdown-button-container  {
  background: rgba(0, 0, 0, 0.12);
}

.dropdown-button-container {
  display: flex;
  height: 100%;
}

.dropdown-button {
  width: 150px;
  margin: auto;
  padding: 8px 16px;
  cursor: pointer;
}

.dropdown ul {
  list-style: none;
  box-shadow: 0 8px 10px rgba(0, 0, 0, 0.5);
  padding: 16px 0;
  border-radius: 4px;
}

.dropdown ul li {
  width: 200px;
  padding: 16px 0;
  border-bottom: 1px solid linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(0,212,255,1) 100%);
}

.dropdown ul li:last-child {
  border-bottom: unset;
}

li a, .dropdown a {
  text-decoration: none;
}

li a:visited, .dropdown a:visited {
  color: inherit;
}

.dropdown ul li:hover {
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.12), rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.12));
}

.dropdown-button-label {
  font-size: 1.2em;
  font-weight: bold;
}

.menu-lable, .dropdown-button-label {
  color: rgb(0, 3, 28);
}